<template>
    <div>
        <el-main>
            <div class="select">
                <el-date-picker
                    v-model="time"
                    type="daterange"
                    range-separator="-"
                    start-placeholder="日期"
                    end-placeholder
                ></el-date-picker>
                <el-input v-model="gid" placeholder="账号" clearable></el-input>
                <el-input v-model="name" placeholder="名字" clearable></el-input>
                <el-input v-model="ip" placeholder="IP" clearable></el-input>
            </div>
            <el-table
                border
                ref="multipleTable"
                tooltip-effect="dark"
                style="width: 100%"
                :header-cell-style="{background:'#f5f7fa'}"
                :default-sort="{prop: 'date', order: 'descending'}"
                :data="tableData"
            >
                <el-table-column prop="date" label="日期" sortable></el-table-column>
                <el-table-column prop="gid" label="账号" sortable></el-table-column>
                <el-table-column prop="name" label="姓名"></el-table-column>
                <el-table-column prop="ip" label="ip地址"></el-table-column>
                <el-table-column prop="action" label="行为"></el-table-column>
            </el-table>
            <el-pagination
                style="float: right;margin-top:8px;"
                background
                :current-page.sync="currentPage"
                @current-change="handleCurrentChange"
                layout="prev, pager, next"
                :page-size="7"
                :total="totalPage"
            ></el-pagination>
        </el-main>
    </div>
</template>

<script>
import axios from "axios";
export default {
    data() {
        return {
            gid:"",
            ip: "",
            time: "",
            name: "",
            currentPage: 1,
            totalPage: 1,
            tableData: [
                {
                    date: "2019-01-01 12:46:25",
                    gid: "215869874",
                    name: "admin",
                    ip: "127.0.0.1",
                    action: "修改员工资料"
                },
                {
                    date: "2019-02-02 13:25:55",
                    gid: "236541555",
                    name: "tset",
                    ip: "127.123.202.111",
                    action: "删除员工"
                },
                {
                    date: "2017-05-21 21:58:59",
                    gid: "589632142",
                    name: "admin",
                    ip: "127.10.10.11",
                    action: "添加员工"
                },
                {
                    date: "2016-11-11 14:14:14",
                    gid:"258963147",
                    name: "admin",
                    ip: "127.0.0.1",
                    action: "修改管理员权限"
                },
                {
                    date: "2017-10-01 00:00:00",
                    gid:'556321478',
                    name: "abc",
                    ip: "12.10.10.221",
                    action: "修改员工资料"
                },
                {
                    date: "2018-11-11 23:23:32",
                    gid:"258974631",
                    name: "admin",
                    ip: "126.200.200.100",
                    action: "修改密码"
                },
                {
                    date: "2018-11-11 24:23:32",
                    gid:"258874631",
                    name: "admin",
                    ip: "126.200.200.100",
                    action: "修改密码"
                }
            ]
        };
    },
    created: function() {
        this.getList();
    },
    mounted: function() {},
    watch: {
        time(newValue, oldValue) {
            this.getList();
        },
        gid(newValue, oldValue) {
            this.getList();
        },
        ip(newValue, oldValue) {
            this.getList();
        },
        name(newValue, oldValue) {
            this.getList();
        },
    },
    methods: {
        //获取table数据
        getList() {
            let here = this;
            axios({
                url: "/loglist",
                params: {
                    date: here.time==''?'':moment(here.stime[0]).format("YYYY-MM-DD hh:mm:ss") + ',' + moment(here.stime[1]).format("YYYY-MM-DD hh:mm:ss"),
                    gid: here.gid,
                    ip: here.ip,
                    name: here.name,
                    currentPage: here.currentPage
                }
            })
                .then(function(response) {
                    let data = response.data;
                    here.tableData = data.data;
                    here.totalPage = data.totalPage;
                })
                .catch(function(error) {
                    console.log(error);
                });
        },
        //当前页数改变时调用
        handleCurrentChange() {
            this.getList();
        },
    }
};
</script>

<style lang="stylus" scoped>
body {
    margin: 0;
}

.btn {
    margin-top: 10px;
    margin-bottom: 10px;
}

.select {
    display: inline;
}

.select, .el-input {
    width: 156px;
    margin-bottom 10px
}
.search {
    position: relative;
    top: -1px;
}
.el-date-editor--daterange.el-input, .el-date-editor--daterange.el-input__inner, .el-date-editor--timerange.el-input, .el-date-editor--timerange.el-input__inner{
    width 240px;
}
</style>


